  --system-danger: #B81616;
  --system-danger_hover: #A41616;
}

.dark {
  /* Dark Mode */
  --ink-0: #141414;
  --ink-100: rgba(254, 254, 254, 0.02);
  --ink-200: rgba(254, 254, 254, 0.04);
  --ink-300: rgba(254, 254, 254, 0.08);
  --ink-400: rgba(254, 254, 254, 0.12);
  --ink-500: rgba(254, 254, 254, 0.16);
  --ink-600: rgba(254, 254, 254, 0.24);
  --ink-700: rgba(254, 254, 254, 0.40);
  --ink-800: rgba(254, 254, 254, 0.64);
  --ink-900: rgba(254, 254, 254, 0.80);
  --ink-1000: rgba(254, 254, 254, 1);

  --system-primary: #0A68F5;
  --system-primary_hover: #0B5EDA;
  
  --system-danger: #B81616;
  --system-danger_hover: #A41616;
}

.placeholder\:text-ink-700::-moz-placeholder{
  color: var(--ink-700);
}

.placeholder\:text-ink-700::placeholder{
  color: var(--ink-700);
}

.hover\:bg-ink-200:hover{
  background-color: var(--ink-200);
}

.hover\:bg-ink-900:hover{
  background-color: var(--ink-900);
}

.hover\:bg-system-danger_hover:hover{
  background-color: var(--system-danger_hover);
}

.hover\:bg-system-primary_hover:hover{
  background-color: var(--system-primary_hover);
}

.hover\:text-ink-1000:hover{
  color: var(--ink-1000);
}

.focus\:outline-none:focus{
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.focus\:ring-1:focus{
  --tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
  --tw-ring-shadow: var(--tw-ring-inset) 0 0 0 calc(1px + var(--tw-ring-offset-width)) var(--tw-ring-color);
  box-shadow: var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
}

.focus\:ring-system-primary:focus{
  --tw-ring-color: var(--system-primary);
}

.group\/item:hover .group-hover\/item\:opacity-100{
  opacity: 1;
}

.group\/tooltip:hover .group-hover\/tooltip\:opacity-100{
  opacity: 1;
}

.group:hover .group-hover\:opacity-100{
  opacity: 1;
}

.group\/tooltip:focus .group-focus\/tooltip\:opacity-100{
  opacity: 1;
}

.dark\:bg-ink-800:is(.dark *){
  background-color: var(--ink-800);
}

.dark\:bg-ink-900:is(.dark *){
  background-color: var(--ink-900);
}

.dark\:text-ink-1000:is(.dark *){
  color: var(--ink-1000);
}

.dark\:ring-ink-800:is(.dark *){
  --tw-ring-color: var(--ink-800);
}

.dark\:hover\:bg-ink-800:hover:is(.dark *){
  background-color: var(--ink-800);
}

@media (min-width: 768px){

  .md\:grid-cols-2{
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}